import React, { useEffect, useRef, useState } from 'react';
import { Box, Button, Flex, Image } from '@chakra-ui/react';
import Slider from 'react-slick';
import { resource } from '../../configs';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const Story = () => {
  const slider = useRef<Slider>(null);
  const [activate, setActivate] = useState(0);

  useEffect(() => {
    slider.current?.slickGoTo(activate);
  }, [activate]);

  return (
    <Box position="relative">
      <Slider
        ref={slider}
        slidesToShow={1}
        slidesToScroll={1}
        arrows={false}
        initialSlide={0}
        afterChange={setActivate}
      >
        <Image src={resource('/home/story-a.png')} />
        <Image src={resource('/home/story-a.png')} />
        <Image src={resource('/home/story-a.png')} />
        <Image src={resource('/home/story-a.png')} />
        <Image src={resource('/home/story-a.png')} />
      </Slider>
      <Box
        position={{ base: 'static', md: 'absolute' }}
        mx={{ base: 'auto', md: 'none' }}
        my={{ base: 8, md: 0 }}
        bottom={8}
        right={8}
        backgroundColor="white"
        rounded={8}
        width={300}
      >
        <Box textAlign="center" py={4} letterSpacing={2} color="black">
          HUMAN AWAKENING
        </Box>
        <Flex justify="space-between" px={4} py={1} backgroundColor="gray.300" roundedBottom={8} fontSize="xs">
          {Array.from({ length: 5 }).map((_, index) => (
            <Button
              size="xs"
              key={index}
              rounded={4}
              p={1}
              onClick={() => setActivate(index)}
              {...(index === activate
                ? {
                    color: 'red',
                    backgroundColor: 'gray.200',
                  }
                : {})}
            >
              {String.fromCharCode(65 + index)}
            </Button>
          ))}
        </Flex>
      </Box>
    </Box>
  );
};

export const AnonStory: React.FC = () => {
  return (
    <Box>
      <Story />
    </Box>
  );
};
