import React from 'react';
import { Box } from '@chakra-ui/react';
import { ClassNames } from '@emotion/react';

interface Source {
  src: string;
  type?: string;
}

interface VideoBgProps {
  sources: Source[];
}

function getDefaultSourceType(src: string): string {
  const split = src.split('.');
  const ext = split[split.length - 1];

  if (ext === 'webm') return 'video/webm';
  if (ext === 'ogg') return 'video/ogg';
  if (ext === 'mov') return 'video/mov';

  return 'video/mp4';
}

export const VideoBg: React.FC<VideoBgProps> = (props) => {
  return (
    <Box overflow="hidden" position="absolute" inset={0} zIndex={0}>
      <ClassNames>
        {({ css }) => (
          <video
            autoPlay
            playsInline
            loop
            muted
            controls={false}
            className={css`
              position: absolute;
              height: 100%;
              width: 100%;
              object-fit: cover;
              z-index: -9999;
            `}
          >
            {props.sources.map(({ src, type }) => {
              type = type || getDefaultSourceType(src);
              return <source key={type + src} src={src} type={type} />;
            })}
          </video>
        )}
      </ClassNames>
      <Box
        w="100%"
        h="20%"
        bgGradient="linear(to-b, transparent, rgb(0,0,0,0.8))"
        zIndex={1}
        position="absolute"
        bottom={0}
        right={0}
      />
    </Box>
  );
};
